export const login = (user) => (
  $.ajax({
    method: 'POST',
    url: '/api/session',
    data: user
  })
)

export const signup = (user) => (
  $.ajax({
    method: 'POST',
    url: '/api/users',
    data: user
  })
)

export const logout = () => (
  $.ajax({
    method: 'DELETE',
    url: '/api/session'
  })
)

export const update = (user) => (
  $.ajax({
    method: 'PATCH',
    url: `/api/users/${user.id}`,
    data: {user}
  })
)

export const fetchUser = (id) => (
  $.ajax({
    method: 'GET',
    url: `api/users/${id}`
  })
)



